$mobile-width: 375px;
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin mobile {
  @media (max-width: #{$tablet-width - 1px}) {
    @content; } }

@mixin tablet {
  @media (max-width: #{$desktop-width - 1px}) {
    @content; } }

@mixin desktop {
  @media (min-width: #{$desktop-width}) {
    @content; } }
